import { Component, OnInit } from '@angular/core';
import { Service } from 'src/app/services/Service';
import { Roadmap } from 'src/app/entities/roadmap';
import { Settings } from 'src/app/entities/settings';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-roadmap-page',
  templateUrl: './roadmap-page.page.html',
  styleUrls: ['./roadmap-page.page.scss'],
})

export class RoadmapPagePage implements OnInit {
  roadmap: Roadmap[] = [];
  loadingRoadmap;
  
  
constructor(
	public service: Service,
	private route: ActivatedRoute,
    public settings: Settings,
) {

   this.settings.langCode = this.route.snapshot.paramMap.get("lang");
   // this.loadRoadmap(this.settings.langCode);
   
   document.documentElement.setAttribute("lang", this.settings.langCode);
   
}

  loadRoadmap(lang: string) {
    this.service.getRecentRoadmap(lang).then((response: Roadmap[]) => {
        if(response['error']){
          this.loadingRoadmap = false;
        } else if (response) {
          this.roadmap = this.roadmap.concat(response);
        }
    });
  }


  ngOnInit() {
  }

}
